import "babel-polyfill"


import Vue from 'vue'

window.apiready = function() {
    window.$api =  $api
    api.toast({
        msg: '',
        duration: 10000,
        location: 'top'
    });
    var dialogBox;
    var ajpush;
    //vue.js
    var myapp = new Vue({
        el: "#wrap",
        data() {
            return {
                isActive: 0,
                headerOffset: {},
                footerOffset: {},
                gFrameIndex: 0,
                keyword: '',
                index: 0, //frame
                cartNum: 0,
                popupVisible: false,
                content: ''
            }
        },
        created() {
            alert('created')

        },
        mounted() {
            this.cartNum = Number(localStorage.getItem('cartNum'));
            this.funIniGroup(0);
            api.toast({
                msg: '',
                duration: 10000,
                location: 'top'
            });
            // this.isRegisterPush();

        },
        methods: {
            toJump(pageName) {
                api.openWin({
                    name: pageName,
                    url: 'widget://html/' + pageName + '.html',
                    animation: {
                        type: "none",
                    },
                })
            },
            initJG() {
                var _this = this;
                ajpush.init(function(ret) {
                    if (ret && ret.status) {
                        //success
                    }
                })
            },
            toMy() {
                api.sendEvent({
                    name: 'orderNum',
                    extra: {
                        success: 'true'
                    }
                });

                this.randomSwitchBtn(3, 'fourth_header', 'my');
                ajpush.setBadge({
                    badge: 0
                });
            },
            // iosframebug
            // iosopen
            showgroup(type) {
                api.setFrameGroupAttr({
                    name: type + 'group',
                    hidden: false
                });
            },
            // ===================================
            // frame
            // ===================================
            switchframe(_index, className) {
                var _this = this;
                _this.isActive = _index;
                _this.$nextTick(function() {
                    _this.headerOffset = $api.offset($api.byId(className));

                    api.setFrameGroupAttr({
                        name: 'indexGroup',
                        rect: {
                            x: 0, //x
                            y: 0, //y
                            w: api.winWidth, //'auto'framex
                            h: api.frameHeight - _this.footerOffset.h //'auto'framey
                        }
                    });
                    api.setFrameGroupIndex({
                        name: 'indexGroup',
                        index: _index
                    });
                })

            },

            randomSwitchBtn(_index, className, pageName) {
                var _this = this;
                if (this.isActive == _index) {
                    return
                };
                if (_index == 2 || _index == 3) {

                    //api.getPrefs
                    api.getPrefs({
                        key: 'lsb_loginStatus'
                    }, function(ret, err) {
                        //(ret.value)

                        if (ret.value && ret.value != "") {
                            //

                            _this.switchframe(_index, className);
                        } else {


                            api.openFrame({
                                name: 'login',
                                url: 'widget://html/login.html',
                                bounces: false,
                                animation: {
                                    type: "none",
                                },
                                pageParam: {
                                    changeID: _index,
                                },
                                slidBackEnabled: false
                            });

                        }
                    })
                } else {
                    _this.switchframe(_index, className)
                }
            },
            funIniGroup(changeID) {
                this.headerOffset = $api.offset($api.byId('first_header'));
                this.footerOffset = $api.offset($api.byId('footer'));
                var _this = this;
                var frames = [{
                    name: 'home',
                    url: 'widget://html/home.html',
                    bgColor: '#fff',
                    bounces: false
                }, {
                    name: 'sort',
                    url: 'widget://html/sort.html',
                    bgColor: '#fff',
                    bounces: false
                }, {
                    name: 'cart',
                    url: 'widget://html/cart.html',
                    bgColor: '#fff',
                    bounces: false,
                    reload: true
                }, {
                    name: 'my',
                    url: 'widget://html/my.html',
                    bgColor: '#fff',
                    bounces: false
                }];


                _this.index = changeID;
                _this.isActive = changeID;

                Vue.nextTick(function() {
                        _this.index == 0 ? _this.headerOffset = $api.offset($api.dom('#first_header')) : '';
                        _this.index == 1 ? _this.headerOffset = $api.offset($api.dom('#second_header')) : '';
                        _this.index == 2 ? _this.headerOffset = $api.offset($api.dom('#third_header')) : '';
                        _this.index == 3 ? _this.headerOffset = $api.offset($api.dom('#fourth_header')) : '';
                        api.closeFrameGroup({
                            name: 'indexGroup'
                        });
                        // alert(':'+api.frameHeight+':'+_this.footerOffset.h)
                        alert(JSON.stringify(_this.footerOffset))
                        api.openFrameGroup({
                            name: 'indexGroup',
                            scrollEnabled: false,
                            rect: {
                                x: 0,
                                y: 0,
                                w: api.winWidth,
                                h: 667
                                // api.frameHeight - _this.footerOffset.h
                            },
                            index: _this.index,
                            frames: frames
                        }, function(ret, err) {

                        })
                    })
                    // })
            },

            // 
            search() {
                var keyword = this.keyword;
                api.openWin({
                    name: 'list',
                    url: 'widget://html/list.html',
                    pageParam: {
                        query: {
                            type: 'search',
                            mark: keyword,
                            name: ""
                        },
                    }
                });
                this.keyword = ''
            },
            maxTo(num) {
                if (Number(num) > 1000) {
                    return ''
                } else {
                    return Number(num)
                }
            }
        },
        created() {

        }
    });
    // 


    // ajpush = api.require('ajpush');

    // myapp.initJG();

    // dialogBox = api.require("dialogBox");
    api.sendEvent({
        name: 'initCart',
        extra: {
            success: 'true'
        }
    });
    api.addEventListener({
        name: 'cartNum'
    }, function(ret, err) {
        if (ret) {
            myapp.cartNum = Number(ret.value.num);
        }
    });
    api.addEventListener({
        name: 'toCart'
    }, function(ret, err) {
        if (ret) {
            myapp.switchframe(2, 'third_header');

        }
    });
    api.addEventListener({
        name: 'login'
    }, function(ret, err) {
        if (ret) {
            api.closeFrameGroup({
                name: 'indexGroup'
            });
            myapp.funIniGroup(ret.value.changeID);
        }
    });

    api.addEventListener({
        name: 'keyback'
    }, function(ret, err) {
        if (ret) {

            dialogBox.alert({
                texts: {
                    title: "",
                    content: "",
                    leftBtnTitle: '',
                    rightBtnTitle: ''
                },
                styles: {
                    bg: '#fff',
                    w: 300,
                    title: {
                        marginT: 20,
                        icon: 'widget://res/gou.png',
                        iconSize: 40,
                        titleSize: 14,
                        titleColor: '#000'
                    },
                    content: {
                        color: '#000',
                        size: 14
                    },
                    left: {
                        marginB: 7,
                        marginL: 20,
                        w: 130,
                        h: 35,
                        corner: 2,
                        bg: '#dadada',
                        color: '#000',
                        size: 12
                    },
                    right: {
                        marginB: 7,
                        marginL: 10,
                        w: 130,
                        h: 35,
                        corner: 2,
                        bg: '#dadada',
                        color: '#000',
                        size: 12
                    }
                }
            }, function(UserClick) {
                if (UserClick.eventType == "right") {
                    dialogBox.close({
                        dialogName: "alert"
                    });
                    api.closeWidget();
                } else if (UserClick.eventType == "left") {
                    dialogBox.close({
                        dialogName: "alert"
                    });
                }
            })
        }
    })
}



